import { css } from 'lit';

export const styles = css`:host{--stroke-thickness: 1.3px;--circular-size: calc(var(--diameter) - var(--stroke-thickness));--scale-factor: 2.95}[part~=label],[part~=value]{font-size:round(var(--diameter)/var(--scale-factor) - var(--stroke-thickness),1px)}[part~=label]{color:var(--text-color)}[part~=track]{stroke:var(--base-circle-color)}:host([variant=danger]){--gradient-stop-color: var(--fill-color-danger)}:host([variant=warning]){--gradient-stop-color: var(--fill-color-warning)}:host([variant=info]){--gradient-stop-color: var(--fill-color-info)}:host([variant=success]){--gradient-stop-color: var(--fill-color-success)}[part~=gradient_start],[part~=gradient_end]{stop-color:var(--gradient-stop-color, var(--fill-color-default))}`;
